@charset "UTF-8";
.register {
  width: 100%;
  height: 100%; }
  .register * {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none; }
  .register a {
    color: #d5d5d5;
    font-size: 12px; }
  .register .main,
  .register .container,
  .register .bgPic {
    width: 100%;
    height: 100%; }
  .register .bgPic {
    background-image: url("../../img/b3_1.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%; }
  .register body {
    overflow: hidden;
    /* //取消滚动条 */ }
  .register .container {
    position: relative; }
  .register .container .register {
    width: 360px;
    height: 380px;
    background-color: rgba(255, 255, 255, 0.9);
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    right: 20%;
    border-radius: 5px;
    padding: 20px 40px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .register .register .title span {
    font-size: 18px;
    color: #333; }
  .register .register .title a::before {
    content: "已有账号>>";
    font-size: 12px; }
  .register .register .title a {
    float: right;
    font-size: 14px;
    color: #111; }
  .register .register .title a:hover {
    color: blue; }
  .register form {
    margin-top: 23px;
    position: relative; }
  .register form > span {
    position: absolute;
    top: -19px;
    right: 0;
    color: green; }
  .register form .default {
    width: 280px;
    height: 32px;
    margin-bottom: 15px;
    padding: 5px 0px; }
  .register form .default input {
    width: 100%;
    height: 100%;
    outline: none;
    border-radius: 3px;
    border: 1px solid #ccc;
    padding-left: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .register form .submit {
    height: 80px;
    position: relative; }
  .register form .submit .notice {
    position: relative;
    top: 10px;
    left: -12px;
    float: right;
    display: block;
    height: 22px;
    font-size: 12px;
    float: right;
    margin-bottom: 8px; }
  .register form .submit .notice a {
    color: #333;
    text-decoration: underline; }
  .register form .submit button {
    display: block;
    width: 280px;
    line-height: 38px;
    outline: none;
    border: none;
    text-align: center;
    border-radius: 3px;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    background: #0aa1ed; }
  .register form .default span {
    color: red;
    font-size: 14px; }



