@charset "UTF-8";
.login {
  width: 100%;
  height: 100%; }
  .login * {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none; }
  .login a {
    color: #d5d5d5;
    font-size: 12px; }
  .login .main,
  .login .container,
  .login .bgPic {
    width: 100%;
    height: 100%; }
  .login .bgPic {
    background-image: url("../../img/b3_1.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%; }
  .login body {
    overflow: hidden;
    /* //取消滚动条 */ }
  .login .container {
    position: relative; }
  .login .container .register {
    width: 360px;
    height: 300px;
    background-color: rgba(255, 255, 255, 0.9);
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    right: 20%;
    border-radius: 5px;
    padding: 20px 40px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .login .title span {
    font-size: 18px;
    color: #333; }
  .login .title a::before {
    content: "没有账号>>";
    font-size: 12px; }
  .login .title a {
    float: right;
    font-size: 14px;
    color: #111; }
  .login .title a:hover {
    color: blue; }
  .login .form {
    margin-top: 23px; }
  .login .form .default {
    width: 280px;
    height: 35px;
    margin-bottom: 15px;
    padding: 5px 0px; }
  .login .form .default span {
    color: red;
    font-size: 14px; }
  .login .form .default input {
    width: 100%;
    height: 100%;
    outline: none;
    border-radius: 3px;
    border: 1px solid #ccc;
    padding-left: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box; }
  .login .form .submit {
    height: 80px; }
  .login .form .submit .notice {
    float: right;
    display: block;
    height: 22px;
    font-size: 12px;
    float: right;
    margin-bottom: 8px; }
  .login .form .submit .notice a {
    color: #333;
    text-decoration: underline; }
  .login .form .submit button {
    display: block;
    width: 280px;
    line-height: 38px;
    outline: none;
    border: none;
    text-align: center;
    border-radius: 3px;
    font-size: 16px;
    color: #fff;
    cursor: pointer;
    background: #0aa1ed; }



