.home {
  position: relative;
  margin: 0;
  padding: 0;
  font-size: 0;
  list-style: none;
  text-decoration: none; }
  .home strong {
    font-size: bold;
    text-decoration: none;
    font-size: 14px; }
  .home a {
    color: #d5d5d5;
    font-size: 12px; }
  .home .home {
    position: absolute;
    width: 100%;
    height: 90px;
    top: 0;
    left: 0; }
  .home .userDeatil {
    position: absolute;
    width: 200px;
    right: 100px;
    top: 50px; }
    .home .userDeatil img {
      margin-top: -25px;
      width: 50px;
      height: 50px;
      float: left; }
  .home .username {
    float: right;
    font-size: 16px;
    height: 20px;
    line-height: 20px;
    cursor: pointer; }
  .home .username span:hover {
    color: blue; }
  .home .nav-book {
    width: 100%;
    min-width: 1040px; }
  .home .nav-book .nav-wrap {
    background: #e3e9e2; }
  .home .nav-book .nav-primary {
    width: 1040px;
    margin: 0 auto;
    padding: 10px 0; }
  .home .nav-book .nav-search {
    margin-left: 145px;
    padding: 10px 0 15px; }
  .home .nav-book form {
    font-size: 0; }
  .home .nav-book .input {
    height: 34px;
    width: 470px;
    border: 0;
    border-radius: 4px;
    -webkit-box-shadow: 2px 2px 2px #666;
            box-shadow: 2px 2px 2px #666;
    text-indent: 15px;
    outline: 0; }
  .home .nav-book .submit {
    position: absolute;
    width: 37px;
    height: 35px;
    border: 0;
    border-radius: 0 4px 4px 0;
    margin-left: -2px;
    background: url("https://img3.doubanio.com/dae/accounts/resources/321e246/music/assets/nav_mu_bg.png?s=1") no-repeat 0 -37px; }
  .home #search_suggest {
    position: absolute;
    top: 58px; }
  .home #search_suggest ul {
    position: relative;
    padding: 0;
    z-index: 100;
    width: 460px;
    border: 1px solid #ddd; }
  .home #search_suggest ul li {
    border-bottom: 1px solid #ccc;
    background: #fff;
    height: 50px; }
  .home #search_suggest ul li:last-of-type {
    border-bottom: 0px; }
  .home #search_suggest ul li a {
    text-decoration: none;
    padding: 6px;
    color: #999;
    display: block; }
  .home #search_suggest ul li a img {
    float: left;
    width: 40px;
    height: 40px; }
  .home #search_suggest ul li a div {
    margin-left: 50px; }
  .home #search_suggest ul li a p {
    margin: 0;
    padding: 5px 0; }
  .home__slide {
    width: 720px;
    height: 0px;
    padding-bottom: 380px; }
  .home__slideWrap {
    width: 720px;
    height: 380px; }
  .home .h-body {
    width: 980px;
    margin: 0 auto;
    position: relative; }
  .home .wrapper {
    margin-top: 18px; }
  .home .slider-fa {
    width: 720px;
    height: 380px;
    float: right; }
  .home .menu {
    float: left;
    padding-bottom: 1px;
    width: 236px;
    border: 2px solid #e0dcd7; }



